body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.row {
  margin: 0 auto;
}

.jumbotron {
  background-image: url("/images/wallpaper.jpg");
}


.navbar {
    background-color: #d2d69a;
    width: 100%;
    display: block;
    font-size: 1.5em;
}

.brand {
    font-weight: bold;
}

.navbar ul {
    margin: 0;
    padding: 0;
}

.navbar li {
    display: inline-block;
    width: 33.33%;
    padding: 10px 0;
    text-align: center;
    color: #3f2828;
    text-shadow: 2px 2px #fffb00;
}

.correct {
    -webkit-animation: flashgreen 1s;
            animation: flashgreen 1s;
}

.incorrect {
    -webkit-animation: flashred 1s;
            animation: flashred 1s;
}

@-webkit-keyframes flashgreen {
    0% {color: #FFCB05;}
    50% {color: #00ff00;}
    100% {color: #FFCB05;}
}

@keyframes flashgreen {
    0% {color: #FFCB05;}
    50% {color: #00ff00;}
    100% {color: #FFCB05;}
}

@-webkit-keyframes flashred {
    0% {color: #FFCB05;}
    50% {color: #ff0000;}
    100% {color: #FFCB05;}
}

@keyframes flashred {
    0% {color: #FFCB05;}
    50% {color: #ff0000;}
    100% {color: #FFCB05;}
}

.shake {
  /* Start the shake animation and make the animation last for 0.5 seconds */
  -webkit-animation: shake 0.7s;
          animation: shake 0.7s;
  width: 30em; 
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg);}
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg);}
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}
.jumbotron {
    min-height: 10px;
    background-color: #d2d69a;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 350px;
    padding-top: 100px;
    margin-bottom: 70px;
    padding-bottom: 0;
}


p {
    border-bottom: #000000;
    text-align: center;
    font-weight: bold;
    color: #3f2828;
    text-shadow: 2px 2px #fffb00;
    font-size: 1.5em;
    margin-bottom: 1em;
    padding-bottom: 1em;
}
.card {
  width: 10.5em;
  display: inline-block;
  margin: 1em;
  background: #eee;
}

.card:hover {
  cursor: pointer;
  border-radius: 5%;
  border: 1px solid #3a3a29;
}

@media only screen and (min-width: 893px) {
  .card {
    margin: 1em 0.5em;
  }
}

